.photo-container {
    h4 {
        font-size: 1.8rem;
    }

    p {
        font-size: 1.2rem;
        text-align: center;
    }

    img {
        width: 100%;
        padding: .2rem;
    }

    button {
        padding: .7rem;
        margin: 1.4rem 0;
        border-radius: 18px;
        font-size: 1rem;
        width: 75%;
        border: solid 1.3px grey;

        &:hover {
            cursor: pointer;
        }
    }

    .download {
        background-color: #004171;
        color: white;

        &:hover {
            background-color: lighten($color: #004171, $amount: 20%);
        }
    }
}